import { mockGigs, mockUsers, mockMessages } from "../data/mockData";

export const getGigs = () => {
  return new Promise((resolve) => {
    setTimeout(() => resolve(mockGigs), 500);
  });
};

export const getGigById = (id: string) => {
  return new Promise((resolve) => {
    setTimeout(() => {
      const gig = mockGigs.find((gig) => gig.id === id);
      resolve(gig);
    }, 500);
  });
};

export const getUserById = (id: string) => {
  return new Promise((resolve) => {
    setTimeout(() => {
      const user = mockUsers.find((user) => user.id === id);
      resolve(user);
    }, 500);
  });
};

export const getMessages = (userId: string) => {
  return new Promise((resolve) => {
    setTimeout(() => {
      const userMessages = mockMessages.filter(
        (msg) => msg.senderId === userId || msg.receiverId === userId,
      );
      resolve(userMessages);
    }, 500);
  });
};

export const sendMessage = (message: any) => {
  return new Promise((resolve) => {
    setTimeout(() => {
      mockMessages.push(message);
      resolve(message);
    }, 500);
  });
};
